import React, { useState } from "react";
import '../css/RepositoryList.css';

const RepositoryList = ({ repositories }) => {
  const [selectedRepo, setSelectedRepo] = useState(null);

  const showDescript = (repo) => {
    setSelectedRepo(repo);
  };

  const selectRepository = (repo) => {
    setSelectedRepo(repo);
  };

  return (
    <div className="repository-list-container">
      <div className="repo-list">
        {repositories.map((repo, index) => (
          <div
            key={index}
            className={`repository-item ${repo === selectedRepo ? 'selected' : ''}`}
            onClick={() => { selectRepository(repo); showDescript(repo); }}
          >
            {repo.name}
          </div>
        ))}
      </div>
      <div className="selected-repo-container">
        {selectedRepo ? (
          <div className="selected-repo">
            <p>{selectedRepo.description}</p>
            <div className="git-container">
              <button className="btn-git-url">
                <a href={selectedRepo.url} target="_blank" rel="noopener noreferrer">
                  GitHub Repository
                </a>
              </button>
            </div>
          </div>
        ) : (
          <div className="initial-message">
            <p>Press any project to know more about it :D</p>
          </div>
        )}
      </div>
    </div>
  );
};

export default RepositoryList;